@import url(https://fonts.googleapis.com/css2?family=ABeeZee&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.4/css/all.min.css);
*{
margin:0;
padding:0;
box-sizing:border-box;
-webkit-tap-highlight-color: rgba(0,0,0,0);
transition:color .1s;
}
:root{
    --body-clr:#f1f1f1;
    --dark-clr:#707070;
    --light-clr:#ffffff;
    --green:linear-gradient(#06D81E, #27B43D);
    --green-text:#06D81E;
    --prm-grd:linear-gradient(170deg, #e7e7e7, #FFFFFF);
    --prm-shadow: 2px 2px 5px #DDDFE3, -2px -2px 5px #FFFFFF;
    --prm-shadow-reverse: -2px -2px 5px #dedede, 2px 2px 5px #e2e2e2;
    --prm-grd-reverse:linear-gradient(315deg, #e4e4e4, #ffffff);
    --secondary-clr:#3C3C59;
    --story-border:linear-gradient(-210deg, #379BDE 50%, #115ED2 80%);
    --border:rgb(255, 255, 255);
    --icons-clr:invert(0);
    --splitter-clr:rgba(114, 114, 114, .2);
    --message-count:#fff;
}
.tap{
    background-color:#f1f1f1;
    background-color:var(--body-clr);
}
.tap.dark{
    --body-clr:#1f1f1f;
    --prm-grd:linear-gradient(-210deg, #1a1a1a 50%, #1a1a1a 80%);
    --prm-grd-reverse:#000;
    --prm-shadow: 2px 2px 5px #131313, -2px -2px 5px #222222;
    --prm-shadow-reverse: -2px -2px 5px #131313, 2px 2px 5px #161616;
    --light-clr:#333;
    --border:rgb(51, 51, 51);
    --icons-clr:invert(100%);
    --secondary-clr:rgb(255, 255, 255);
    --icons-clr:invert(100%);
    --splitter-clr:rgba(0, 0, 0, 0.2);
    --message-count:#fff;
}

button{
    border:0;
    outline:none;
    font-size:.8rem;
    color:#3C3C59;
    color:var(--secondary-clr);
}
p{
    font-family:"noto-sans";
    color:#3C3C59 !important;
    color:var(--secondary-clr) !important;
}
h4{
    font-family:"noto-sans";
    color:#3C3C59;
    color:var(--secondary-clr);
    font-weight: 600;
}
a{
    text-decoration: none;
    color:#3C3C59;
    color:var(--secondary-clr);
    outline:none;
}
a:active{
    color:#707070;
    color:var(--dark-clr);
}
ul{
    list-style:none;
}
.splitter{
    content:"";
    display:block;
    border-top:1px solid rgba(114, 114, 114, .2);
    border-top:1px solid var(--splitter-clr);
    border-bottom:1px solid #ffffff;
    border-bottom:1px solid var(--light-clr);
    width:100%;
    position:absolute;
    left:0;
    top:0;
}
.splitter.btm{
    bottom:-.3rem;
}
p.gr{
    color:#06D81E !important;
    color:var(--green-text) !important;
}
p.gr-count{
    width:25px;
    height:25px;
    font-size:.7rem;
    display:flex;
    justify-content:center;
    align-items:center;
    color:#fff !important;
    color:var(--message-count) !important;
    background:linear-gradient(#06D81E, #27B43D);
    background:var(--green);
    border-radius:50%;
}
.g{
    display:grid;
}
.g.g-col-3{
    grid-template-columns:50px 1fr 80px;
    grid-column-gap:.5rem;
}
.g.c{
    place-content:center;
}
.b{
    border:1px solid;
}
.f{
    display:flex;
}
.f.wp{
    flex-wrap: wrap;
}
.f.f-cl{
    flex-direction:column;
}
.jc-sb{
    justify-content:space-between;
}
.jc-sa{
    justify-content:space-around;
}
.jc-c{
    justify-content:center;
}
.ai-sb{
    align-items:space-between;
}
.ai-c{
    align-items:center;
}
.primary{
    background: linear-gradient(170deg, #e7e7e7, #FFFFFF);
    background: var(--prm-grd);
    box-shadow: 2px 2px 5px #DDDFE3, -2px -2px 5px #FFFFFF;
    box-shadow: var(--prm-shadow);
}
.prm{
    background: linear-gradient(170deg, #e7e7e7, #FFFFFF);
    background: var(--prm-grd);
    box-shadow: 2px 2px 5px #DDDFE3, -2px -2px 5px #FFFFFF;
    box-shadow: var(--prm-shadow);
    padding:.5rem;
    border-radius: .5rem;
}
.prm.crcl{
    border-radius:50%;
    min-width:50px;
    height:50px;
}
.prm:active{
    background: linear-gradient(315deg, #e4e4e4, #ffffff);
    background: var(--prm-grd-reverse);
    box-shadow:  -2px -2px 5px #dedede, 2px 2px 5px #e2e2e2;
    box-shadow:  var(--prm-shadow-reverse);
}
.r{
    position:relative;
}
.tap{
  padding: 0 .8rem;
  height:100vh;
}

header{
  padding:.8rem 0;
}
.togg-flat-checkbox{
	position: relative;
	display: inline-block;
	width:55px;
	height: 30px;
}
.togg-flat-checkbox input{
	display: none;
}
.togg-flat-checkbox .togg-flat-checkbox-slider{
	position: absolute;
	cursor: pointer;
	top:0; left: 0; right: 0; bottom: 0;
	background:#efefef;
	transition: .3s;
	border:3px solid var(--border);
	box-shadow: 1px 1px 1px rgba(0,0,0,0.1);
}
.togg-flat-checkbox .togg-flat-checkbox-slider:before{
	position: absolute;
	content:"";
	top:3px;
	left:-2px;
	background-color:#efefef;
	transition: .3s;
}
.togg-flat-checkbox .togg-flat-checkbox-slider.round:before{
	width:43%;
	height: 43%;
	border-top-left-radius: 50px;
	border-top-right-radius: 50px;
	-webkit-transform: rotateZ(-45deg);
	        transform: rotateZ(-45deg);
	background:white;
	box-shadow:inset 0px 0px 0px;
}
.togg-flat-checkbox input[type="checkbox"]:checked + .togg-flat-checkbox-slider.round:before{
	background:yellow;
	-webkit-transform: translateX(32px) rotateZ(45deg);
	        transform: translateX(32px) rotateZ(45deg);
	border-top-left-radius: 50px;
	border-top-right-radius: 50px;
	box-shadow:inset 0.3px 0.4px 1px 0.3px rgba(0,0,0,0.3);
}
.togg-flat-checkbox input[type="checkbox"]:checked + .togg-flat-checkbox-slider{
	background:black;
}
.togg-flat-checkbox .togg-flat-checkbox-slider.round{
	border-radius: 35px;
}
.logo h5{
  margin-left:.5rem;
  font-size:1.3rem;
  margin-top:.5rem;
  color:var(--secondary-clr);
  font-family: 'ABeeZee', sans-serif;
}
.friend_stories{
    overflow-x:auto;
    padding: .8rem .5rem;
}
.storiesScrollBar{
    
}
.story + .story{
    margin-left:1.2rem;
}
.story p{
    margin-top:.2rem;
}
.story:first-child p {
    color:var(--dark-clr);
}
.friend-story .friend-image{
    border-radius:100%;
    overflow:hidden;
    width:50px;
    height:50px;
    border:3px solid #115ED2;
    padding:.2rem;
}
.friend-story .friend-image img{
    border-radius:50%;
   width:100%;
   height:100%;
   object-fit:cover;
   object-position: center;
}
.tags{
    overflow-x:auto;
    padding:.5rem 0;
}
.tag{
    display:block;
}
.tag + .tag{
    margin-left:.5rem;
}
.friendsChats{
    overflow-y:auto;
    height:60%;
}
.chat{
    padding:1rem 0;
}
.fchat{
    border:2px solid var(--border);
}
.fchat:last-child{
    margin-bottom:1rem;
}
.fchat + .fchat{
    margin-top:.8rem;
}
.fpic{
    border-radius:50%;
    overflow:hidden;
    width:50px;
    height:50px;
    border:2px solid var(--border);
}
.fpic img{
    width:100%;
}
.fchatinfo{
}

.bar{
    margin:0 .8rem;
    padding:.5rem 0rem 0 0rem;
    background-color:var(--body-clr);
    height:3.5rem;
    position:fixed;
    bottom:0;
    left:0;
    right:0;
}

.navitemico{
    width:25px;
    height:25px;
}
